<template>
  <div>
    <el-dialog
      width="500px"
      :title="(isCreate ? '新增':'编辑')+'仓库'"
      :visible.sync="isVisible"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      @close="cancel">
      <el-form inline ref="warehouseInfo" :model="warehouseInfo" :rules="formRules" label-width="80px" class="text-c">
        <el-row>
          <el-col>
            <el-form-item label="名称" prop="warehouseName">
              <el-input maxlength="30" v-model="warehouseInfo.warehouseName" class="w160" placeholder=""/>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col>
            <el-form-item label="所属地区" prop="warehouseArea">
              <el-input maxlength="50" v-model="warehouseInfo.warehouseArea" class="w160" placeholder=""/>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col>
            <el-form-item label="所属机构" prop="warehouseOrgUuid">
                <!--<el-select v-model="warehouseInfo.warehouseOrgUuid" class="w160">-->
                  <!--<el-option-->
                    <!--v-for="(org,index) in org"-->
                    <!--:key="index"-->
                    <!--:label="org.orgName"-->
                    <!--:value="org.orgUuid"-->
                  <!--/>-->
                <!--</el-select>-->
              <el-cascader
                class="w160"
                :options="options"
                change-on-select
                v-model="warehouseInfo.warehouseOrgUuidList"
              ></el-cascader>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col class="pos-r">
            <el-form-item label="详细地址" prop="warehouseAddress">
              <el-input :disabled="!warehouseInfo.warehouseAddress" v-model="warehouseInfo.warehouseAddress" type="textarea" class="w160" :autosize="{maxRows:7}" maxlength="200" resize="none" ref="address" placeholder="请输入仓库详细地址，最长200字符"/>
              <i class="el-icon-location pos-a posButton" @click="chooseAddress(warehouseInfo)"></i>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col>
            <el-form-item label="描述" prop="warehouseDesc">
              <el-input type="textarea" :autosize="{maxRows:7}" maxlength="200"  v-model="warehouseInfo.warehouseDesc" class="w160" resize="none" placeholder="请输入描述,最长200字符"/>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col>
            <el-button @click="cancel">取消</el-button>
            <el-button type="primary" @click="save">提交</el-button>
          </el-col>
        </el-row>
      </el-form>
    </el-dialog>
    <g-map
      :show.sync="isMapShow"
      :addressInfo="addressInfo"
      @getAddress="getAddress"
    />
  </div>
</template>

<script>
import { getWarehouseDetail, updateWarehouse } from '@/api/warehouse'
import GMap from './Map'
import store from '@/store'

export default {
  name: 'WarehouseCreate',
  data () {
    return {
      warehouseInfo: {
        warehouseName: '',
        warehouseArea: '',
        warehouseOrgUuid: '',
        warehouseAddress: '',
        warehouseDesc: '',
        warehouseLat: '',
        warehouseOrgUuidList: []
      },
      isVisible: false,
      formRules: {
        warehouseName: [{required: true, message: '请输入仓库名称', trigger: 'blur'}],
        warehouseArea: [{required: true, message: '请输入仓库区域', trigger: 'blur'}],
        warehouseAddress: [{required: true, message: '请选择详细地址', trigger: 'blur'}],
        warehouseOrgUuidList: [{required: true, message: '请选择仓库所属机构', trigger: 'blur'}],
        warehouseDesc: [{ min: 0, max: 2000, message: '描述长度请不要超过2000字', trigger: 'change' }]
      },
      isMapShow: false,
      addressInfo: {},
      options:[]
    }
  },
  props: {
    show: {
      type: Boolean,
      default: false
    },
    org: {
      type: Array,
      required: true
    },
    orgList: {
      type: Array,
      required: true
    },
    nodeData: {
      type: Object,
      required: true
    },
    isCreate: {
      type: Boolean,
      required: true
    }
  },
  watch: {
    show (isShow) {
      if (isShow) {
        // 编辑
        if (!this.isCreate) {
          this.setToEditState()
        }
      }
      this.isVisible = isShow
    },
    org (org) {
      this.options = org
    },
  },
  methods: {
    setToEditState () {
      this.warehouseInfo = Object.assign({},this.warehouseInfo, this.nodeData)
      this.findPid(this.warehouseInfo.warehouseOrgUuid)
    },
    chooseAddress (data) {
      this.isMapShow = true
      this.addressInfo = {
        lat: data.warehouseLat || null,
        lng: data.warehouseLng || null,
        address: data.warehouseAddress}
    },
    getAddress (lat, lng, address) {
      this.warehouseInfo.warehouseLat = lat
      this.warehouseInfo.warehouseLng = lng
      this.warehouseInfo.warehouseAddress = address
      this.$refs.address.$el.children[0].focus()
      this.$refs.address.$el.children[0].blur()
      console.log(this.warehouseInfo)
    },
    save () {
      this.$refs.warehouseInfo.validate(isValid => {
        if (isValid) {
          this.warehouseInfo.warehouseOrgUuid = this.warehouseInfo.warehouseOrgUuidList[this.warehouseInfo.warehouseOrgUuidList.length-1]
          updateWarehouse(this.warehouseInfo).then(res => {
            if (!res.error) {
              this.$message({
                message: (this.isCreate ? '新增':'修改') + '成功！',
                type: 'success'
              })
            }
            store.dispatch('getWarehouseList')
            this.cancel()
            this.$emit('updateTree')
          })
        }
      })
    },
    cancel () {
      this.warehouseInfo = {
        warehouseName: '',
        warehouseArea: '',
        warehouseOrgUuid: '',
        warehouseAddress: '',
        warehouseDesc: '',
        warehouseLat: '',
        warehouseOrgUuidList: []
      }
      this.$refs.warehouseInfo.resetFields()
      this.$emit('update:show', false)
      this.isVisible = false
    },
    // 寻找用户所属机构的上级机构，组成数组供级联选择框使用
    findPid (orgUuid) {
      let lastLevel = this.orgList.find(item => {
        return item.id == orgUuid
      })
      if (lastLevel) {
        this.warehouseInfo.warehouseOrgUuidList.unshift(lastLevel.id)
      }
      if (lastLevel.pId != '' && !lastLevel.root) {
        this.findPid(lastLevel.pId)
      }
    }
  },
  components: { GMap }
}
</script>

<style lang="scss" scoped>
  .posButton{
    top: 5px;
    right: -25px;
    font-size: 20px;
    cursor: pointer;
  }
  .w160{
    width: 160px;
  }
</style>



