<template>
  <div>
    <nav-button>
      <el-button-group>
        <el-button icon="fdddfont icon-add-new" @click.native="handleAdd()">新增</el-button>
        <!--<el-button icon="fdddfont icon-cancel-new" @click.native="cancel()" :disabled="!isButtonIsDisabled()">删除</el-button>-->
      </el-button-group>
    </nav-button>
    <common-wrapper>
      <common-table :flex="230">
        <template slot="table">
          <el-table
            ref="roleTable"
            height="100%"
            stripe
            :border="true"
            tooltip-effect="light"
            v-loading="isListLoading"
            :data="tableData"
            @selection-change="selected => selectRow = selected">
            <el-table-column type="index" :index="indexMethod" label="序号" width="50"></el-table-column>
            <el-table-column prop="warehouseName" sortable label="名称" width="150"
                             :show-tooltip-when-overflow="true"></el-table-column>
            <el-table-column prop="warehouseArea" sortable label="所属地区" width="150"
                             :show-tooltip-when-overflow="true"></el-table-column>
            <el-table-column prop="warehouseOrgUuid" sortable label="所属机构" :formatter="formatOrgName" width="150"
                             :show-tooltip-when-overflow="true"></el-table-column>
            <el-table-column prop="warehouseAddress" label="详细地址" min-width="200"
                             :show-tooltip-when-overflow="true"></el-table-column>
            <el-table-column prop="warehouseDesc" label="描述" min-width="200"
                             :show-tooltip-when-overflow="true"></el-table-column>
            <el-table-column label="操作" width="190">
              <template slot-scope="scope">
                <el-tooltip class="item" effect="light" content="编辑" placement="bottom">
                  <el-button size="mini" round icon="fdddfont icon-modify"
                             @click.stop="handleEdit(scope.$index, scope.row)"></el-button>
                </el-tooltip>
                <el-tooltip class="item" effect="light" content="删除" placement="bottom">
                  <el-button size="mini" round icon="fdddfont icon-delete"
                             @click.stop="handleDel(scope.$index, scope.row)"></el-button>
                </el-tooltip>
              </template>
            </el-table-column>
          </el-table>
        </template>
        <template slot="pagination">
          <el-pagination background layout="total, prev, pager, next"
                         :current-page.sync="pagination.pageNum"
                         :page-size="pagination.pageSize"
                         :total="pagination.total"
                         @current-change="changePage"
          ></el-pagination>
        </template>
      </common-table>
    </common-wrapper>
    <!-- 新增仓库-->
    <warehouse-create
      :show.sync="isCreateFormVisible"
      :isCreate="isCreate"
      :orgList="orgnization"
      :nodeData="clickedNodeData"
      :org="cascaderOrg"
      @updateTree="queryList"
    />
  </div>
</template>

<script>
  import {generateTree} from '@/utils/generateTree'
  import NavButton from '@/components/navButton'
  import NavPremissonButton from '@/components/navButton/navPremissonButton'
  import CommonWrapper from '@/components/commonWrapper'
  import CommonTable from '@/components/commonTable'
  import {getWarehouseTable, deleteWarehouse} from '@/api/warehouse'
  import {getOrgTree} from '@/api/organization'
  import WarehouseCreate from './Components/WarehouseCreate'
  import {mixins} from '@/mixins'

  export default {
    name: 'WarehouseManager',
    mixins: [mixins],
    data () {
      return {
        warehouseList: this.$store.getters.warehouseList,
        tableData: [],
        orgnization: [], // 公用组织机构
        cascaderOrg: [],
        selectRow: [], // 列表选中列
        isListLoading: false, // 列表加载
        isCreateFormVisible: false,
        clickedNodeData: {},
        pagination: { // 翻页数据
          pageNum: 1,
          pageSize: 10,
          total: 0
        },
        searchParams: {}, // 查询参数
        isCreate: true
      }
    },
    mounted () {
      this.queryList()
      this.getOrgList()
    },
    methods: {
      // 组织机构列表
      getOrgList () {
        getOrgTree().then(res => {
          this.orgnization = res
          // 级联选择机构数据
          res.forEach(item => {
            item.value = item.id
            item.label = item.orgName
          })
          this.cascaderOrg = generateTree(res)
          // 删除children为空的数据
          this.deleteNullChildren(this.cascaderOrg)
        })
      },
      // 查询/翻页公用方法
      queryList () {
        this.updateTable(
          {
            pageNum: this.pagination.pageNum,
            pageSize: this.pagination.pageSize
          }
        )
      },
      // 列表翻页
      changePage (pageNum) {
        this.pagination.pageNum = pageNum
        this.queryList(this.searchParams)
      },
      // 生成列表序号
      indexMethod (index) {
        return (index + 1) + (this.pagination.pageNum - 1) * this.pagination.pageSize
      },
      updateTable (params) {
        this.isListLoading = true
        getWarehouseTable(params).then(res => {
          this.tableData = res.item
          this.pagination.total = res.total
          this.pagination.pageNum = res.pageNum
          this.pagination.pageSize = res.pageSize
          this.isListLoading = false
          if (res.item.length == 0 && res.pageNum > 1) {
            this.pagination.pageNum--
            params.pageNum--
            this.updateTable(params)
          }
        })
      },
      formatOrgName (row) {
        let org = this.orgnization.find(item => item.id === row.warehouseOrgUuid)
        if (org) {
          return org.orgName
        } else {
          return ''
        }
      },
      handleAdd () {
        this.isCreate = true
        this.isCreateFormVisible = true
        this.clickedNodeData = {}
      },
      isButtonIsDisabled () {
        return !!this.selectRow.length
      },
      // 列表翻页
      changePage (pageNum) {
        this.pagination.pageNum = pageNum
        this.queryList(this.searchParams)
      },
      handleEdit (index, row) {
        this.clickedNodeData = row
        this.isCreate = false
        this.isCreateFormVisible = true
      },
      handleDel (index, row) {
        this.$confirm('删除仓库后该仓库下所有数据将不可见，确认删除该仓库吗?', '提示', {
          type: 'warning'
        }).then(() => {
          const params = {
            warehouseIds: [row.warehouseUuid]
          }
          deleteWarehouse(params).then(res => {
            if (!res.error) {
              this.$message({
                message: '删除成功！',
                type: 'success'
              })
              this.queryList()
            }
          })
        }).catch(() => {
        })
      },
      deleteNullChildren (data) {
        data.forEach (item => {
          if(item.children && item.children.length>0) {
            this.deleteNullChildren(item.children)
          } else if (item.children && item.children.length == 0) {
            delete item.children
          }
        })
      }
    },
    components: {
      NavButton,
      NavPremissonButton,
      CommonWrapper,
      CommonTable,
      WarehouseCreate
    }
  }
</script>

<style scoped>

</style>



